import { useState } from "react";
import axios from "axios";
import { FaLightbulb } from "react-icons/fa";

const Light = ({ id, light }) => {

  const switchLight = (newState) => {
  console.log('Apply state ' + JSON.stringify(newState));
  axios
    .put(
      `http://localhost/api/local/lights/${id}/state`,
      newState
    )
  };

  const [state, setState] = useState(light.state)

  return (
      <div className="lightContainer textLight">
          <div className="iconContainer">
            <FaLightbulb/>
          </div>
          <div className="textContainer">
          <p>{ light.name }</p>
          </div>
          <div className="switchContainer">
            <label className="switch">
              <input type="checkbox"

                onChange={(e) => switchLight({'on': e.currentTarget.checked})}
              />
              <span className="slider"></span>
            </label>
          </div>
          <div className="slideContainer">
            <input type="range" min="1" max="254" defaultValue="50" className="slider" id="myRange"
              onChange={(e) => switchLight({'bri': e.currentTarget.value})}
            />
          </div>
        </div>
  )
}

export default Light
