const Scenes = ({groupId, scenes}) => {
  return (
    <>
    {Object.entries(scenes).filter(scene => scene.group === groupId).map(([id, scene]) => (
      <div className="groupContainer expanded textLight" key={id}>
        <h3>{scene.name}</h3>
    </div>
    ))}
    </>
);
}

export default Scenes
